import './App.css';
import AppMenu from './common/Header/Menu';
import Footer from './common/Footer/Footer';
import SideBarMenu from './common/SideBarMenu/SideBarMenu';
import Content from './common/SideBarMenu/Content';
import Layover from './common/Layover';
import React from "react";
import initializer from './common/AppInit'

class AppComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {wsState: "init", dialog: null};
    this.wsCallback = this.handleWSState.bind(this);
    this.showDialog = this.showDialog.bind(this)
  }

  handleWSState(state) {
    this.setState({wsState: state});
  }

  showDialog(dialog) {
    this.setState({dialog:dialog});
  }

  render() {
    console.log("state=", this.state.wsState);
    var layover = null;
    if (this.state.wsState != "ready" && this.state.wsState != "offline") {
      layover = <div className="layover"><Layover state={this.state.wsState}/></div>;
    }
    
    return (
      <div className="container">
        <Content />
        <div className="top-right">
          <a target="_blank" href="https://lekh.app/help/lekhboard.html" className="help-link">Help</a>
        </div>
        <div className="top-left-menu">
          <AppMenu showDialog={this.showDialog}/>
        </div>
        <div className="side-bar">
           <SideBarMenu />
          </div>
        <div className="footer-area">
          <Footer/>
        </div>
        {layover}
        {this.state.dialog}
      </div>
    );
  }

  componentDidMount() {
    initializer.init(this.wsCallback);
  }
}

export default AppComponent;
